<template>
  <div class="home-page page">
    <Header title="HFSV兑换"/>
    <div class="addr-box">
      <textarea class="addr" v-model="walletAddr" placeholder="请输入FSV主网钱包地址"></textarea>
      <input class="input-box" v-model="transNum" type="number" placeholder="请输入兑换数量">
    </div>
    <div class="tip-line">当前钱包的HFSV余额: {{myHfsv}} HFSV</div>
    <div class="tip-line">可兑换 {{canTransFsv}} FSV</div>
    <div class="trans-btn" @click="toTrans">兑换</div>
    <div class="record-list">
      <div class="list-title"><span>兑换记录</span></div>
      <div class="loading-box" v-show="loading">
        <van-loading color="#333333" />
      </div>
      <div class="record-item" v-for="r in recordList" :key="r.ts">
        <div>兑换钱包地址：{{r[1]}}</div>
        <div>兑换数量：{{r[0] | formatNum}} HFSV</div>
        <div>兑换时间：{{r.ts | formatTime}}</div>
      </div>
    </div>
  </div>
</template>

<script>
import Header from "@/components/Header"
import BigNumber from 'bignumber.js'

export default {
  data () {
    return {
      account: '',
      myHfsv: '--',
      canTransFsv: '--',
      walletAddr: '',
      transNum: '',
      recordList: [],
      loading: false
    }
  },
  mounted () {
    console.log(this.$coinContract)
    this.getAccount()
  },
  watch: {
    myHfsv (val) {
      this.canTransFsv = val
    }
  },
  methods: {
    async getAccount () {
      this.$web3.eth.getAccounts().then( async accs => {
        if (accs.length > 0) {
          this.account = accs[0]
          this.getMyBalance()
          this.getLogs()
          let status = await this.getStatus()
          if (status) {
            this.toChangePaused()
          }
        } else {
          console.log('no account')
        }
      })
    },
    async getLogs () {
      this.recordList = []
      this.loading = true
      let ts = await this.$coinContract.methods.getLogs(this.account).call({
        from: this.account
      })
      let logs = []
      for (let i = ts.length - 1; i >= 0; i--) {
        let log = await this.$coinContract.methods.getLog(ts[i]).call({
          from: this.account
        })
        logs.push({...log, ts: ts[i]})
      }
      this.loading = false
      this.recordList = logs
    },
    async getStatus () {
      return await this.$coinContract.methods.paused().call({
        from: this.account
      })
    },
    async toChangePaused () {
      await this.$coinContract.methods.changePaused().send({
        from: this.account
      })
    },
    async getMyBalance () {
      let balance = await this.$coinContract.methods.balanceOf(this.account).call()
      let b = BigNumber(balance).dividedBy(BigNumber('1e18')).toFixed(4, 1)
      this.myHfsv = b
    },
    async toTrans () {
      if (!this.walletAddr) {
        this.$toast({
          message: '请输入fsv主网钱包地址',
          position: 'top'
        })
        return
      }
      if (!this.transNum) {
        this.$toast({
          message: '请输入兑换数量',
          position: 'top'
        })
        return
      }
      let tNum = BigNumber(this.transNum).multipliedBy(BigNumber('1e18'))
      if (tNum.isLessThan(1)) {
        this.$toast({
          message: '兑换数量过小',
          position: 'top'
        })
        return
      }
      BigNumber.config({ EXPONENTIAL_AT: 30 })
      console.log(tNum.toString())

      this.$toast.loading({
        forbidClick: true,
        message: '',
        duration: 0
      })
      this.$coinContract.methods.burn(
          this.walletAddr,
          tNum.toString(),
          new Date().getTime()
      ).send({
        from: this.account
      }).then(receipt => {
        console.log(receipt)
        this.$toast.clear()
        this.$toast.success('兑换成功')
        this.walletAddr = ''
        this.transNum = ''
        this.getMyBalance()
        this.getLogs()
      }).catch(err => {
        console.error(err)
        this.$toast.clear()
        this.$toast.fail('兑换失败：' + JSON.stringify(err))
      })
    }
  },
  filters: {
    formatTime (ts) {
      return new Date(Number(ts)).toLocaleString()
    },
    formatNum (num) {
      return BigNumber(num).dividedBy(BigNumber('1e18')).toFixed(4, 1)
    }
  },
  components: {
    Header
  }
}
</script>

