import Web3 from 'web3'
import Vue from 'vue'
import coinAbi from './coinAbi.json'

let web3Provider
if (window.ethereum) {
    web3Provider = window.ethereum
    try {
        window.ethereum.enable()
    } catch (error) {
        console.error("User denied account access")
    }
} else if (window.web3) {
    web3Provider = window.web3.currentProvider
} else {
    web3Provider = new Web3.providers.HttpProvider('http://localhost:8545')
}

Vue.prototype.$web3 = new Web3(web3Provider)
Vue.prototype.$coinAddr = '0xA961711Cbac0936eda4b5DaEa7776b43384312CC'
Vue.prototype.$coinAbi = coinAbi
Vue.prototype.$coinContract = new Vue.prototype.$web3.eth.Contract(
    Vue.prototype.$coinAbi,
    Vue.prototype.$coinAddr,
    {
    }
)
